	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog by Laura Domine","subtitle":"","copyright":"Powered by Gatsby. © All rights reserved.","menu":[{"label":"Articles","path":"/"},{"label":"About","path":"/about/"},{"label":"Contact","path":"/contact/"}],"author":{"name":"Laura Domine","email":"ldomine@stanford.edu","telegram":"#","twitter":"#","github":"Temigo","rss":"#","vk":"#"}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/posts/luminous/","categorySlug":"/categories/sf/"},"frontmatter":{"title":"Luminous","date":"2017-10-29","category":"SF","description":"'Luminous' is a science-fiction short story published by Greg Egan in 1995. Starting as a dark thriller, it leads us very quickly on a trail of surprising ideas about mathematics, physics and what we call 'truth'."}}},{"node":{"fields":{"slug":"/posts/piraha/","categorySlug":"/categories/languages/"},"frontmatter":{"title":"The Pirahã language","date":"2017-10-28","category":"Languages","description":"Human languages share few features that distinguish them from other form of animal communication. One of the most famous is recursion. In any language one can always form an arbitrarily long sentence. Always? Not quite. According to Daniel Everett, up to now the only non-native of the Pirahã Amazonian tribe who has ever understood their language, the Pirahã language does have a sentence of maximal length. Of course this statement stirred a durable controverse among linguists and prompted Noam Chomsky to criticize him as a 'charlatan'. Yet his story is interesting and, in my opinion, worth listening to."}}}]}},"pathContext":{}}

